	import listCell from '@/components/mix-list-cell';
	import {
			mapState,
			mapMutations
	} from 'vuex';
	let startY = 0, moveY = 0, pageAtTop = true;
    export default {
		components: {
			listCell
		},
		data(){
			return {
				coverTransform: 'translateY(0px)',
				coverTransition: '0s',
				moving: false,
				list: [],
				amount: '',
				imgUrl: 'http://47.112.113.139',
				commission: ''
			}
		},
		onLoad(){
			},
		onReady () {
			this.sendRequest();
		},
		onShow(){
			this.sendRequest2();
		},








    computed: {
			...mapState(['hasLogin','userInfo','userMsg'])
		},
    methods: {
			...mapMutations(['logout']),
			async sendRequest(){
				try {

					const list = await this.$request.post('/business/category', {});
					this.list = list.data.data;
				} catch (error) {
					console.error('error:', error);
				}
			},
			async sendRequest2(){
				try {
					const amount = await this.$request.post('/balance/index',{
						header:{
							token: this.userInfo.token
						}
					})
					const commission = await this.$request.post('/balance/income-this-month-and-last-month',{
						header:{
							token: this.userInfo.token
						}
					})

					this.amount = amount.data.data;
					this.commission = commission.data.data;
				} catch (error) {
					console.error('error:', error);
				}
			},

			loginOut() {	//登出
				let _this = this;
				if(!this.hasLogin){
					uni.showModal({
						title: '请先登录！',
						content: '您目前尚未登录',
						success: function (res) {
								if (res.confirm) {
									uni.navigateTo({
										url: '/pages/login/login'
									})
								} else if (res.cancel) {
										console.log('用户点击取消');
								}
						}
				  });
				} else {
					uni.showActionSheet({
						itemList: ['退出当前账号'],
						success: function (res) {
								if(res.tapIndex === 0) {
									uni.showToast({
										title: '账号已退出',
											icon: 'none'
									});
									_this.logout();  //退出登录
									_this.amount = '';
								}
						},
						fail: function (res) {
								console.log(res.errMsg);
						}
				  });
				}
			},
			// 淘宝登录授权
			async goToTaoBao() {
				if(!this.hasLogin){
					uni.navigateTo({
						url: '/pages/login/login'
					})
				} else {
					const taoVerify = await this.$request.post('/authorization/taobao-verify',{
						header: {
							'token': this.userInfo.token
						},
					})
					if(taoVerify.data.code === 0){
						uni.showToast({
								title: taoVerify.data.msg,
								icon: 'none',
								position: 'bottom'
						});
						return false;
					}
					this.$common.taoLogin();
				}
			},

			copyInv() {	//复制邀请码，不兼容h5
				uni.setClipboardData({
						data: this.userInfo.mobile,
						success: function () {
							uni.showToast({
								title: '复制邀请码成功，快去分享给好友吧',
								icon: 'none'
							});
								// console.log('复制成功');
						}
				});
			},

			/**
			 * 统一跳转接口,拦截未登录路由
			 * navigator标签现在默认没有转场动画，所以用view
			 */
			navTo(url){
				if(!this.hasLogin){
					url = '/pages/login/login';
				}
				uni.navigateTo({
					url
				})
			},

			toSchool(item){
				uni.navigateTo({
					url: `/pages/school/school-list?id=${item.id}&name=${item.name}`
				})
			},

			/**
			 *  会员卡下拉和回弹
			 *  1.关闭bounce避免ios端下拉冲突
			 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
			 *    transition设置0.1秒延迟，让css来过渡这段空窗期
			 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
			 */
			coverTouchstart(e){
				if(pageAtTop === false){
					return;
				}
				this.coverTransition = 'transform .1s linear';
				startY = e.touches[0].clientY;
			},
			coverTouchmove(e){
				moveY = e.touches[0].clientY;
				let moveDistance = moveY - startY;
				if(moveDistance < 0){
					this.moving = false;
					return;
				}
				this.moving = true;
				if(moveDistance >= 80 && moveDistance < 100){
					moveDistance = 80;
				}

				if(moveDistance > 0 && moveDistance <= 80){
					this.coverTransform = `translateY(${moveDistance}px)`;
				}
			},
			coverTouchend(){
				if(this.moving === false){
					return;
				}
				this.moving = false;
				this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
				this.coverTransform = 'translateY(0px)';
			}
		}
}
